.header {
  position: fixed;
  height: 7%;
  width: 100%;
}

.btn {
  position: fixed;
  font-size: large;
    outline: none;
    -webkit-box-shadow: none;
    box-shadow: none;
}

.btn.clear {
  left: 1%;
}

.btn.run {
  right:1%;
}

.btn.close {
  right:1%;
}

.fullscreen {
    position: fixed;
    vertical-align: middle;
    text-align: center;
}

.input {
  position: fixed;
  top: 7%;
  font-size: 70px;
  width: 100%;
  height: 90%;
  padding: 10px;

    border: none;
    overflow: auto;
    outline: none;

    -webkit-box-shadow: none;
    box-shadow: none;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: pink;
}


